/* Product20.css */

.product20 {
  text-align: left; /* Align content to the left */
  overflow: hidden;
  margin-top: 120px; /* Adjust top margin to position product20 below header */
}

.product20-content {
  display: flex;
  align-items: flex-start; /* Align items to the start (left-aligned) */
  padding: 30px; /* Increased padding for better spacing */
}

.product-image-container {
  flex: 1;
  margin-right: 30px; /* Add spacing between image and details */
}

.product-image {
  width: 100%;
  height: auto; /* Adjust height if needed */
  object-fit: cover;
}

.product-details-container {
  flex: 1;
}

.product-title {
  font-size: 28px; /* Increase title font size */
  margin-bottom: 15px; /* Increased margin for better separation */
}

.product-reviews {
  font-size: 16px;
  color: #666;
  margin-bottom: 15px; /* Increased margin for better separation */
}

.star-filled {
  color: gold; /* Filled star color */
}

.star-empty {
  color: #ccc; /* Empty star color */
}

.reviews-count {
  margin-left: 5px;
}

.product-price {
  font-size: 20px; /* Increased font size */
  color: #333;
  margin-top: 10px; /* Adjusted margin */
}

.quantity-section {
  margin-top: 30px; /* Increased top margin for better spacing */
}

.quantity-section label {
  display: block;
  font-size: 16px;
  margin-bottom: 8px; /* Adjusted margin */
}

.quantity-input {
  display: flex;
  align-items: center;
}

.quantity-btn {
  background-color: black; /* Changed background color to black */
  color: white;
  border: none;
  padding: 10px; /* Adjusted padding */
  font-size: 18px;
  cursor: pointer;
  margin: 0 5px; /* Added margin for spacing between buttons */
}

input[type='number'] {
  width: 60px;
  font-size: 16px;
  padding: 10px; /* Adjusted padding */
  text-align: center;
}

.add-to-cart-btn {
  background-color: black; /* Changed background color to black */
  color: white;
  border: none;
  padding: 16px 24px; /* Increased padding */
  font-size: 22px; /* Increased font size */
  cursor: pointer;
  margin-top: 20px; /* Adjusted margin */
}

.add-to-cart-btn:hover {
  background-color: #333; /* Darker color on hover */
}

.product-description {
  margin-top: 30px; /* Increased top margin for better spacing */
}

.product-description h3 {
  font-size: 22px; /* Increased font size */
  margin-bottom: 12px; /* Adjusted margin */
}
